import React, { useState, useEffect } from 'react'
import TabletImg from '../src_31138.png'
import DesktopImg from '../src_59505.png'
import MobileImg from '../src_70719.png'
import landscapeImg from '../src_45452.png'

const Resize = () => {

    const [windowWidth, widthHandler] = useState(window.innerWidth)
    const [visible, visibleHandler] = useState(true)

    useEffect(() => {
        function WindowResizeEvent() {
            const currentwidth = window.innerWidth
            widthHandler(currentwidth)
        }
        window.addEventListener('resize', WindowResizeEvent)

        return () => { debugger; window.removeEventListener('resize', WindowResizeEvent) }
    }, [])

    return (<>
        <button onClick={() => visibleHandler(!visible)} disabled={ (windowWidth >= 481) ? false : true}>{visible ? 'Hide' : 'Show'}</button>
        {visible &&
            <div>
                <div>
                    <p>This content support all device</p>
                    {(windowWidth >= 768) && <p>This content is support only desktop and tablet</p>}
                    {(windowWidth <= 480) && (windowWidth >= 320) && <p>This content is only show only Portrait Mobile</p>}
                </div>
                <div>Current Window Width : {windowWidth}</div><hr></hr>
                {(windowWidth >= 1025) && <div><p>Desktop</p><img src={DesktopImg} alt='Desktop' style={{ width: '200px', height: '200px' }} /></div>}
                {(windowWidth <= 1024) && (windowWidth >= 768) && <div><p>Tablet</p><img src={TabletImg} alt='Tablet' style={{ width: '200px', height: '200px' }} /></div>}
                {(windowWidth <= 767) && (windowWidth >= 481) && <div><p>Mobile (Landscape)</p><img src={landscapeImg} alt='Mobile (Landscape)' style={{ width: '200px', height: '200px' }} /> </div>}
                {(windowWidth <= 480) && (windowWidth >= 320) && <div><p>Mobile(Portrait)</p><img src={MobileImg} alt='Mobile (Portrait)' style={{ width: '200px', height: '200px' }} /></div>}
            </div>}
    </>)
}

export default Resize
